.photos{
    margin: 20px;
    li{
        list-style: none;
    }
    h1{
        color: red;
    }
    .img-container{
        display: flex;
        justify-content: space-between;
        flex-wrap: wrap;
       img {
           margin: 10px;
           height: 150px;
           width: 150px;
           border: 1px solid gray;
           border-radius: 5px;
           box-shadow: 5px 5px 2px -1px rgba(0, 0, 255, 0.2);
       }
    }
}